import React, { useState, useEffect } from 'react';
import './App.scss';
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css"
import { STEPS } from './utils/constants'
import { Header } from './Components/Header'
import { Content } from './Components/Content'
import { storage } from './storage/storage'
import { SelectStep } from './Components/SelectStep'
import { Messaging } from './utils/messages'
import { isDevEnv, updateScreen } from './utils/utils'
import { Footer } from './Components/Footer'


console.log('ENV:', process.env.NODE_ENV)


class App extends React.Component {
  state = storage.state

  changeStore = storageProps => {
    const newState = { ...this.state, ...storageProps }
    this.setState(newState)
    storage.state = newState
  }

  componentDidMount() {
    console.log('****init')
    const onmessage = msg => {
      updateScreen(msg, this.changeStore)
    }

    Messaging.init(onmessage)
  }

  render() {
    console.log('state', this.state)
    return (
      <div id="outer">
        <Header />
        { isDevEnv && <SelectStep store={this.state} changeStore={this.changeStore} /> }
        <Content store={this.state} changeStore={this.changeStore} />
        { !this.state.loading && <Footer/> }
      </div>
    );
  }

}

function App2() {
  const [store, setStore] = useState(storage)

  const changeStore = storageProps => {
    setStore({...store, ...storageProps})
  }

  useEffect(() => {
    // Update the document title using the browser API

    console.log('****init')
    Messaging.onmessage = (msg) => {
      updateScreen(msg, changeStore)
    }

    Messaging.init()
  });

  return (
    <div id="outer">
      <Header />
      <SelectStep store={store} changeStore={changeStore} />
      <Content store={store} changeStore={changeStore} />
    </div>
  );
}

export default App;
