import React from 'react'
import logo from '../media/newLogo.png'
import en from '../media/gb.png'
import ro from '../media/ro.png'

export const Header = props => (
  <div id="header">
    <div className="container">
      <div id="logo" className="newLogo">
        <a href="#">
          <img src={logo} alt="Raiffeisen Bank" />
        </a>
      </div>
      <div id="lang" className="lang">
        <div>
          <a className="en">
            <img src={en} />
            EN
          </a>
        </div>
        <div>
          <a className="ro">
            <img src={ro} />
            RO
          </a>
        </div>
      </div>
    </div>
  </div>
)