import React, { useState, useEffect } from 'react'
import { RightPart } from './RightPart'
import { LeftPart } from './LeftPart'
import { storage } from '../storage/storage'
import { Step_1 } from './step_1'
import { Step_2 } from './step_2'
import { Step_3 } from './step_3'
import { Step_4 } from './step_4'
import { Step_5 } from './step_5'
import { Step_6 } from './step_6'
import { Step_7 } from './step_7'
import { STEPS } from '../utils/constants'
export const Steps = props => {

  const { store: { step } } = props

  const getStep = (step) => {
    switch(step) {
      case STEPS.STEP_1:
        return <Step_1 {...props} />
      case STEPS.STEP_2:
        return <Step_2 {...props} />
      case STEPS.STEP_3:
        return <Step_3 {...props} />
      case STEPS.STEP_4:
        return <Step_4 {...props} />
      case STEPS.STEP_5:
        return <Step_5 {...props} />
      case STEPS.STEP_6:
        return <Step_6 {...props} />
      case STEPS.STEP_7:
        return <Step_7 {...props} />
      default:
        return null
    }
  }
  return(
    <React.Fragment>
      {getStep(step)}
    </React.Fragment>
  )
}


export const DynamicContainer = props => {

  return (
    <React.Fragment>
      <Steps {...props} />
    </React.Fragment>
  )
}