import React from 'react'
import { STEPS } from '../utils/constants'

const getTitle = step => {
  switch(step) {
    case STEPS.STEP_1:
      return 'Bine ai venit la Raiffeisen Online'
    case STEPS.STEP_2:
      return 'Bine ai venit la Raiffeisen Online'
    case STEPS.STEP_3:
      return 'Un SMS care contine codul de activare'
    case STEPS.STEP_4:
    case STEPS.STEP_5:  
    case STEPS.STEP_6:  
    case STEPS.STEP_7:  
        return 'RAIFFEISEN BANK'
     default:
       return ''
    }
}
export const TitleField = ({ store }) => {

  return(
    <div id="masthead">
      <h1>{getTitle(store.step)}</h1>
    </div>
  )
}