import React from 'react'
import mobile from '../media/mobile.png'
import { STEPS } from '../utils/constants'

const ModuleTop = props => (
  <div className="module mobile top">
    <img src={mobile} alt="mobile" />
    <div className="top-content-wrapper">
      <strong>Acces si prin telefonul mobil!</strong>
        <br />
        Descarca si instaleaza aplicatia Raiffeisen 
        Smart Mobile disponibila pentru telefoanele 
        si tabletele cu sistem de operare IOS
        <a href="#" >(iPhone)</a>
         si <a href="#">Android</a>. Pentru orice alt sistem de operare 
         poti accesa varianta <a href="#">web</a>.
    </div>
  </div>
)

const ModuleMainStep1 = props => (
  <div className="module main">
    <div>
      <p>
      <strong>Cum obtii serviciul Raiffeisen Online?</strong>
      <br />
        Acceseaza sectiunea "Cum devii client" pe <a href="#">www.raiffeisenonline.ro</a><span> </span>
         si afla toate conditiile pe care trebuie sa le indeplinesti 
        pentru a putea beneficia de serviciul de internet banking oferit de Raiffeisen Bank.
      </p>
    </div>
    <div>
      <p>
      <strong>Nu divulga niciodata elementele de securitate.</strong>
        <br />
        Raiffeisen Bank nu-ti va trimite niciodata e-mail-uri sau sms-uri prin care-ti
        solicita sa accesezi adrese web sau link-uri pentru a te conecta la Raiffeisen Online.
        Site-ul original este securizat, daca ai suspiciuni, paraseste imediat site-ul.
      </p>
    </div>
  </div>
)

const ModuleMainStep2 = props => (
  <div className="module main">
    <div>
      <p>
      <strong>Generare cod autentificare/autorizare</strong>
      <br />
        Atat pentru autentificare in aplicatie cat si pentru
        autorizare plati tastati butonul DIGSIG. Dupa
        introducerea in token a codului PIN introduceti
        codul Challenge afisat in aplicatia Raiffeisen Online 
        si apoi apasati tasta ENT. Tokenul va afisa codul de
        autentificare sau autorizare pe care trebuie sa-l
        introduceti in aplicatie pentru a accesa serviciul sau
        a efectua plati.
        <br />
        <a href="#">Acceseaza Demo</a>
        
      </p>
    </div>
  </div>
)

const getModule = step => {
  switch(step) {
    case STEPS.STEP_1:
    case STEPS.STEP_3:
    case STEPS.STEP_4:
    case STEPS.STEP_5:
    case STEPS.STEP_6:
    case STEPS.STEP_7:
      return <ModuleMainStep1 />
    case STEPS.STEP_2:
      return <ModuleMainStep2 />
    default:
      return null
    }

}

export const RightPart = props => {
  return (
    <div className="right">
      <ModuleTop />
      {getModule(props.store.step)}
    </div>
  )
}